import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
        sliderModal: false,
        gallerySliderId: '',
        gallerySliderImageId: '',
		main: {
            isEmpty: 1 
        },
        ourimpact:{
            isEmpty: 1            
        },
        mission:{
            isEmpty: 1
        },
        students:{
            isEmpty: 1
        },
        studentslist: {
            isEmpty: 1
        },
        team:{
            isEmpty: 1
        },
        news:{
            isEmpty: 1
        },
        newsletter:{
            isEmpty: 1
        },
        programs:{
            isEmpty: 1
        },
        partners:{
            isEmpty: 1
        },
        about: {
            isEmpty: 1
        },
        history: {
            isEmpty: 1            
        },
        case: {},
        cases: {
            isEmpty: 1
        },
        portfolio: {
            isEmpty: 1
        },
        contacts: {
            isEmpty: 1
        },
        projects: {
            isEmpty: 1
        },
        singleProjects: {
            isEmpty: 1
        },
        preloader: true,
        activeProjectSlug: '',
        activeProjectCategory: '',
        nextProject: {
            slug: '',
            category: ''
        }
	},
	mutations: {
        sliderModal(state, val) {
            state.sliderModal = val;
        },
        gallerySliderId(state, val) {
            state.gallerySliderId = val;
        },
        gallerySliderImageId(state, val) {
            state.gallerySliderImageId = val;
        },

		setData(state, payload) {
			state[payload.page] = payload.data;
        },
        setStudents(state, payload) {
            var students = {};
            payload.forEach(element => {
                students[element.slug] = element;
            });
            state.students = students;
        },
        setStudentsList(state, val) {
            state.studentslist = val
        },
        setProject(state, payload) {
            payload.data["posterBg"] = payload.data.acf['project-content']['bg-img']['url'];
            payload.data["year"] = payload.data.acf['project-content']['year'];
            Vue.set(state.singleProjects, payload.page, payload.data);
        },
        setPreloader(state, val) {
            state.preloader = val;
        },
        setActiveProject(state, payload) {
            state.activeProjectSlug = payload.project_slug;
            state.activeProjectCategory = payload.slug;
        },
        setNextProject(state, payload) {
            Vue.set(state.nextProject, 'slug', payload.slug);
            Vue.set(state.nextProject, 'category', payload.category);
        }
	}
});