import Vue from 'vue';
import VueRouter from 'vue-router';
import VueHeadful from 'vue-simple-headful';
import $store from './components/store';


//Pages
import Main from './pages/main.vue';




Vue.use($store);
Vue.use(VueRouter);
Vue.use(VueHeadful);

window.config = {
	langDefault:'ru',
	domain:"",
	api:"",
	langs:{
		ru:'Рус',
		en:'Eng',
		ua:'Укр'
	}
};

// const langChanged = window.location.pathname.split('/')[1];
// (langChanged in window.config.langs ? localStorage.langCode = langChanged : localStorage.langCode = window.config.langDefault);

const routesList = [
	{path :  '/',  name: '', component: Main }


];

const router = new VueRouter({
	mode: 'history',
	routes: routesList,
	scrollBehavior(to, from, savedPosition) {
		return {
			x: 0,
			y: 0
		}
	}
});

new Vue({
	el: "#app",
	router,
	$store
});
